        display(card){
            this.singleCard = card
            this.isHidden = true;
            
        //    this.cardTitle.forEach((value,index) => {
        //        this.singleCard.push(value);
        //        console.log(value)
        //        console.log(index)
        //    });
        },
        hidden(){
            return this.isHidden = false
        }
    },
   
}
</script>
